.pagination {
  display: flex;
  justify-content: flex-end; }
  .pagination .wrapper {
    background: #FFFFFF;
    border: 1px solid #E1E1E1;
    box-sizing: border-box;
    border-radius: 4px; }
  .pagination .btn {
    padding: 7px 10px;
    text-decoration: none;
    display: flex;
    align-items: center;
    justify-content: center;
    margin: 0 4px;
    font-weight: 600;
    font-size: 13px;
    line-height: 18px;
    letter-spacing: -0.2px;
    color: #333333; }
    .pagination .btn:last-child {
      margin-right: 0; }
    .pagination .btn-iconed {
      padding: 0; }
    .pagination .btn.active {
      background: #79dbff; }
  .pagination .btn-group {
    display: flex;
    align-items: center;
